.user-card {
  max-width: 100%;
  color: hsl(200, 100%, 100%);
  border-radius: 15px;
  background-color: hsl(235, 46%, 20%);
}
.user-info {
  display: flex;
  align-items: center;
  background-color: hsl(246, 80%, 60%);
  border-radius: 15px;
  padding: 3em;
}
.profile-picture {
  margin: 2em;
  background-color: white;
  border-radius: 50%;
  width: 55px;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.profile-picture img {
  width: 80px;
}

.card-assigner {
  color: hsl(236, 100%, 87%);
  font-size: 16px;
}

.user-name {
  font-size: 20px;
}

.time-nav {
  max-width: 100%;
  list-style: none;
  display: flex;
  justify-content: space-around;
  gap: 20px;
  padding: 1.6em;
}

.nav-link {
  color: hsl(235, 45%, 61%);
  cursor: pointer;
  background: none;
  border: none;
  font-size: 16px;
}

.nav-link:hover {
  color: #fff;
}

.active {
  color: hsl(200, 100%, 100%);
}
